import axios from 'axios';
export const reportTypePaths = {
    ["cost" /* cost */]: 'reports/costs/',
    ["storage" /* storage */]: 'reports/inventory/storage/',
    ["instance_type" /* instanceType */]: 'reports/inventory/instance-type/',
};
export function runReport(reportType, query) {
    const path = reportTypePaths[reportType];
    return axios.get(`${path}?${query}`);
}
