import { dashboardDefaultFilters, dashboardStateKey, getQueryForWidget, } from './dashboardCommon';
export const selectDashboardState = (state) => state[dashboardStateKey];
export const selectWidgets = (state) => selectDashboardState(state).widgets;
export const selectWidget = (state, id) => selectWidgets(state)[id];
export const selectCurrentWidgets = (state) => selectDashboardState(state).currenWidgets;
export const selectWidgetQueries = (state, id) => {
    const widget = selectWidget(state, id);
    return {
        previous: getQueryForWidget(widget, Object.assign({}, dashboardDefaultFilters, { time_scope_value: -2 })),
        current: getQueryForWidget(widget, dashboardDefaultFilters),
        tabs: getQueryForWidget(widget, Object.assign({}, dashboardDefaultFilters, { resolution: 'monthly' })),
    };
};
